<template>
  <div id="container">
    <div id="profile">
      <div class="user-pic">
        <img v-if="user.photo !== 'https://server.gold-ticket.uz/storage/app/'" :src="user.photo" :alt="user.name">
        <img v-else src="https://gold-ticket.uz/tgs/avatar.png" :alt="user.name">
      </div>
      <div class="user-name">{{ this.user.name }}</div>
      <div class="user-tickets-count">
        <ion-icon name="ticket-outline" class="ai-icon"></ion-icon> {{ tickets_total }}
      </div>
      <div class="user-btn-tools" v-if="user.lang === 'ru'">
        <router-link to="/my-tickets" tag="div" class="user-tickets-btn btn ">Мои тикеты
          <ion-icon name="chevron-forward-outline" class="arrow-right-icon"></ion-icon>
        </router-link>
        <router-link to="/participation" tag="div" class="offers-for-user btn">Повысить шансы
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>
        </router-link>
        <router-link to="/terms" tag="div" class="offers-for-user btn">Условия использования
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>
        </router-link>
        <router-link to="/policy" tag="div" class="offers-for-user btn">Оферта
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>
        </router-link>
      </div>
      <div class="user-btn-tools" v-else>
        <router-link to="/my-tickets" tag="div" class="user-tickets-btn btn ">Mening ticketlarim
          <ion-icon name="chevron-forward-outline" class="arrow-right-icon"></ion-icon>
        </router-link>
        <router-link to="/participation" tag="div" class="offers-for-user btn">Imkoniyatni oshirish
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>
        </router-link>
        <router-link to="/terms" tag="div" class="offers-for-user btn">Foydalanish shartlari
          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>
        </router-link>
<!--        <router-link to="/policy" tag="div" class="offers-for-user btn">Oferta-->
<!--          <ion-icon name="chevron-forward-outline"class="arrow-right-icon"></ion-icon>-->
<!--        </router-link>-->
      </div>
    </div>

    <BottomNavigationMenu active="profile" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";
export default {
  name: "Profile",
  data: () => ({
    user_id: 0,
    user: null,
    tickets_total: 0,
  }),
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  async mounted() {
    const userData = window.Telegram.WebApp.initDataUnsafe.user;

    // this.user_id = 5296876609
    this.user_id = userData.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    axios.get('https://server.gold-ticket.uz/api/ticket/' + this.user_id)
        .then(response => {
          if (response.data.data) {
            this.tickets_total = response.data.total
          }
        })
        .catch(error => console.log(error));
  }
}
</script>

<style scoped>
@media screen and (min-height: 734px) {
  #container {
    margin-bottom: -10px !important;
    position: relative;
    top: -90px;
  }
  #profile {
    margin-top: 150px !important;
  }
}

@media screen and (min-height: 700px) {
  #container {
    margin-bottom: -10px;
    position: relative;
    top: -500px;
  }
  #profile {
    margin-top: 150px !important;
  }
}

@media screen and (min-height: 652px) {
  #container {
    margin-bottom: -5px !important;
    position: relative;
    top: -100px;
  }
  #profile {
    margin-top: 110px;
  }
}

@media screen and (min-height: 498px) {
  #container {
    margin-bottom: -5px;
    position: relative;
    top: -100px;
  }
  #profile {
    margin-top: 110px;
  }
}
</style>
